d;font:1em "Kranky";background-color:#171717;text-transform:uppercase;cursor:pointer;-webkit-transition:0.4s;transition:0.4s;letter-spacing:1px;-webkit-transform:translateY(-40px);transform:translateY(-40px);z-index:2}.random__btn:hover{border-radius:20px;letter-spacing:3px}.names{width:90%;display:flex;justify-content:start;align-items:center;border-bottom:1px solid #171717}.name__element{padding:0 10px 10px 0;font-size:1.3rem;font-weight:900}.contact,.address{width:90%}.contact__element,.address__element{display:flex;align-items:center;justify-content:space-between;padding:10px;border-bottom:1px solid rgba(71,71,71,0.479)}.contact__element>span:first-child,.address__element>span:first-child{font-weight:900}.contact__element>,.address__element>{padding:0 5px}.contact__element:last-child,.address__element:last-child{border-bottom:none}.weather{padding-top:30px;min-height:70vh}.weather__title{width:90%;padding:0 10px 10px 0;font-size:1.3rem;font-weight:900;text-align:left;border-bottom:1px solid #171717}.weather__element{width:90%;display:flex;align-items:center;justify-content:space-between;border-bottom:1px solid rgba(71,71,71,0.479)}.weather__element:last-child{border-bottom:none}.weather__element-text{width:60%}.temperature__symbol{font-weight:700}.map{width:100%;height:60vh;background-color:#171717;margin:0;padding:0}.footer{box-sizing:border-box;min-height:50vh;width:100%;padding:30px 0 0;background-color:#171717;display:flex;justify-content:left;flex-direction:column;align-items:center;border-image-width:0}.contact__title{color:#f1da1d;border-bottom:2px solid;width:90%;padding:0 10px 10px 0;font-size:1.3rem;font-weight:900;text-align:left}.form{width:80%;display:flex;flex-direction:column;align-items:center;justify-content:center}.form-group{width:100%}.form-control{box-sizing:border-box;background-color:transparent;border:none;width:100%;color:#f1da1d;border-bottom:2px solid #f1da1d;resize:none;font-family:"Lato";padding:10px 0 10px 5px;margin-top:10px}.form-control:focus{outline:none}button.send{background-color:#f1da1d;border:none;margin:20px 0 auto auto;padding:10px 20px;cursor:pointer;font-family:"Lato";font-weight:700;text-transform:uppercase}button.send:hover{background-color:white}.social{padding-top:50px;padding-bottom:50px;width:100%;display:flex;align-items:center;justify-content:space-evenly}.chart{background-color:#171717;display:flex;align-items:center;justify-content:center;padding:30px 0;width:100%}.App{width:100vw;max-width:1200px;margin:0 auto;min-height:100vh;background-color:#a69303}.person,.weather{padding:50px 0;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}body{margin:0;font-family:"Lato";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}@media (max-width: 599px){body{font-size:1rem}}@media (min-width: 600px){body{font-size:1.2rem}}@media (min-width: 900px){body{font-size:1.3rem}}@media (min-width: 1200px){body{font-size:1.4rem}}*{margin:0;padding:0;box-sizing:border-box}



